.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  width: 1280px; /* Consider using max-width only to avoid horizontal scrolling on smaller screens */
   /* border: 1px solid; */
  margin-bottom: 70px;
}

.dep {
  font-family: "Inter", sans-serif;
  width: 400px;
  font-size: 30px;
  font-weight: 500;
  line-height: 30px;
  margin-bottom: 20px;
  margin-top: 70px;
}

.profiles {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  border: 1px solid #ebeaea;
  background: #ffffff;
}

.profile-card {
  /* border: 0.6px solid rgb(197, 197, 197); */
  width: 17%; /* Adjust width for smaller screens to ensure cards do not become too narrow */
  height: 320px;
  padding: 0px 20px 32px 20px; /* Corrected padding syntax */
}
.profile-image{
  position: relative;
  width: 100%;
  height: 70%;
  /* max-width: 300px; */
}
.profile-image img {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  margin-top: 50px;
  width: 100%;
  height: 100%;
  display: block;
 
}

 .overlay{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .3s ease;
  background-color: #6B469C;

} 
.profile-image:hover .overlay{
  /* height: 100%; */
  opacity: 1;
} 
.text {
  color: white;
  font-size: 20px; /* Adjust as needed */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.profile-info {
  text-align: center;
 
}
.profile-info h3 {
  /* text-align: center; */
  font-family: "Inter", sans-serif;
font-size: 19px;
font-weight: 600;
line-height: 17px;
letter-spacing: 0em;
/* text-align: center; */

}


.profile-info p {
  color: #555;
  font-family: "Inter", sans-serif;
font-size: 18px;
font-weight: 400;
padding-right: 10px;
line-height: 23px;
letter-spacing: 0em;
text-align: center;

}

.cta-section {
  width: 50%; /* Adjust width for smaller screens */
  height: 300px;
  background-color: #6a5192;
  color: white;
  padding: 30px 20px;
}

.cta-section .cta-content {
  padding-top: 100px;
  max-width: 50%;
  margin: 0 auto;
  text-align: center;
}

.cta-section h2 {
  font-family: "Inter", sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  text-align: center;
  margin-bottom: 20px;
}

.cta-section button {
  background: white;
  color: #800080;
  border: none;
  padding: 10px 15px;
  cursor: pointer;
  border-radius: 30px;
  transition: background-color 0.3s ease;
}

.cta-section button:hover {
  background-color: #f0f0f0;
}

/* Responsive adjustments */
@media (max-width: 1024px) {
  .profile-card {
    width: 23%;
   /* height: 10%; */
  /* Make profile cards take up half the width on smaller screens */
   
  }
  
  h1 {
    font-size: 30px; /* Adjust font size for smaller screens */
    margin-top: 30px; /* Adjust top margin */
    margin-bottom: 20px; /* Adjust bottom margin */
  }
}

/* Further adjustments for mobile screens */
@media (max-width: 768px) {
  .profile-card {
    width: 33%; /* Each profile card takes full width on very small screens */
  }
}
@media (max-width: 600px) {
  .profile-card {
    width: 50%; /* Each profile card takes full width on very small screens */
  }
}
