   position: relative;
    top: 80px;
   /* z-index: 1;  */
   display: flex;
   margin-bottom: 100px;
}
.left img{
  filter: brightness(50%);
   height:40vw;
    width:50vw;
}
.right img{
  height:40vw;
    filter:brightness(50%);
