.footer{
width: 1440px;
height: 595px;
top: 773px;
z-index: -1;
border-width: 0px, 0px, 0px, 0px;
border-style: solid;
border-color: #282829;
display: flex;

}

.one{
width: 25%;
display: flex;
padding-left: 120px;
flex-direction: column;
background-color: #ffffff;
padding: 20px;
}
.logo-container {
  text-align: center;
  margin-bottom: 20px;
  width: 144px;
height: 51px;
position: relative;
top: 37px;
left: 34px;
padding: 0px, 0px, 0px, 0px

}
.logo-container img {
  width: 144px;
  height: 51.08px;
  
}
.subscribe-container{
  width: 261px;
height: 83px;
position: relative;
top: 90px;
left: 30px;
border-radius: 47px;
border: 1px;

}
.subscribe-container h2 {
  color: #333333;
  margin-bottom: 10px;
  text-align: center;
  font-family: "Inter", sans-serif;;
font-size: 14px;
font-weight: 400;
line-height: 20px;
letter-spacing: 0.01em;
text-align: left;

}

.subscribe-container form {
  display: flex;
  justify-content: center;
}

.subscribe-container input[type="email"] {
  border: none;
  border-bottom: 1px solid #333333;
  margin-right: 40px;
  padding: 5px;
}

.subscribe-container button {
  border: none;
  background-color: #333333;
  color: #ffffff;
  padding: 5px 10px;
  cursor: pointer;
}

.info-container {
  margin-top: 20px;
  line-height: 1.6;
  color: #343333;
  font-family: "Inter", sans-serif;;
font-size: 16px;
font-weight: 500;
line-height: 21px;
letter-spacing: 0.01em;
text-align: left;
width: 287px;
height: 126px;
position: relative;
top: 190px;
left: 30px;


}



.two{
  width: 25%;

  border-left: 1px solid black;
  border-right: 1px solid black;
display: flex;
background-color: #FFFFFF;
}
.up1{
  position: relative;
  top: 80px;
  left: 60px;
  width: 119px;
height: 114px;
}
.up1 p{
font-family: "Inter", sans-serif;
font-size: 14px;
font-weight: 400;
line-height: 20px;
letter-spacing: 0.01em;
text-align: left;

}
.down .p1{
  position: relative;
  width: 273px;
  height: 110px;
  top: 430px;
  right: 46px;

  font-family:"Inter", sans-serif;
font-size: 14px;
font-weight: 400;
line-height: 22px;
letter-spacing: 0.01em;
text-align: left;

}
.three{
  width: 50%;
display: flex;
  justify-content: space-around;
  padding: 20px;
  background-color: #ffffff;
  border-top: 1px solid #ffffff;
}

.footer-section h3 {
  margin-bottom: 15px;
  font-size: 16px;
}

.footer-section ul {
  list-style-type: none;
}

.footer-section ul li {
  margin-bottom: 10px;
  color: #333;
}

.social-media {
  position: relative;
  top:350px;
  right: 290px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.icons{
  display: flex;
}
.social-media a {
  margin-right: 10px;
}

.social-media img {
  width: 24px;
  height: 24px;
}

.credits {
  position: relative;
  top: 380px;
  right: 27px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.credits p {
  margin-bottom: 5px;
}

.infra{
  padding-top: 52px;
  font-family: "Inter", sans-serif;
font-size: 14px;
font-weight: 400;
line-height: 28px;
letter-spacing: 0.01em;
text-align: right;
  position: relative;
  left: 300px;
}
.links{
  padding-top: 60px;
font-family: "Inter", sans-serif;
font-size: 14px;
font-weight: 400;
line-height: 28px;
letter-spacing: 0.01em;
text-align: left;
}
.bottum{
  display: flex;
  height: 70px;
}
.bottum li{
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 20px;
  list-style: none;
}



