
.course-container {
    display: flex;
    position: relative;
    top: 120px;
    z-index: -1;
    max-width: 1200px;
    margin-bottom: 50px;
}
.static{
    position: relative;
    overflow: auto; 
    overflow: hidden;
    width: 491px;
    flex: 1;
    padding-right: 20px; 
    background: #F0F3F8;
}
.text-section {
    position: sticky;
    top: 0px;
    display: flex;
    flex-direction: column;
    width: 35vw;
    height: 762px;
    /* flex: 1; */
    padding-right: 20px; 
    background: #F0F3F8;
    margin-bottom: 50px;
}
.cont{
    margin-left: 70px;
    text-align: left;
width: Fixed (349px);
height: Hug (108px);
gap: 6px;

}
h1{
    font-family: Inter;
font-size: 24px;
font-weight: 500;
line-height: 32px;
letter-spacing: 0em;
text-align: left;
width: 255px;
height: 32px;

}
h3{
    padding-top: 15px;
    text-align: left;
    padding-left: 70px;
    width: 426px;
}

.p1{
margin-top: 5px;
margin-left: 70px;
 font-family: "Inter", sans-serif;;
font-size: 15px;
font-weight: 400;
line-height: 24px;
letter-spacing: 0em;
text-align: left;
width: 340px;
}

.image-gallery {
    margin-left: 50px;
    margin-bottom: 70px;
    padding-bottom: 90px;
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.image-gallery .image-large {
    width: 845px;
    height: 562px;
}


.image-gallery img {
    width: 100%;
    display: block;
    object-fit: cover;
}



@media (max-width: 1024px) {
    /* .course-container {
        flex-direction: column;
        align-items: center; 
        max-width: 100%; 
        padding: 0 20px; 
    } */
   .static{
    margin: 10px;
    width: 100%;
   }
    .text-section {
        width: 37%; /* Full width for smaller screens */
        padding-right: 5px; /* Remove right padding as it's now full width */
        padding-left: 7px; /* Remove right padding as it's now full width */
        margin-bottom: 30px; /* Adjust bottom margin for spacing */
    }

    .cont {
        margin-left: 0; /* Remove left margin for alignment */
        width: auto; /* Allow content container to adjust width automatically */
        padding: 0 0px; /* Add padding for text content */
    }
  .p1{
    margin-left: 2%;
  }
    h1,  h3 {
        width: auto; /* Allow text elements to adjust width automatically */
        margin-bottom: 10; /* Align text to the start */
        padding-left: 0; /* Remove left padding for alignment */
    }

    .image-gallery {
        margin-left: 0; /* Remove left margin for alignment */
        width: 77%; /* Allow image gallery to use full width */
    }

    .image-gallery .image-large {
        width: 100%; /* Full width for the large image to be responsive */
        height: auto; /* Adjust height automatically to maintain aspect ratio */
    }
}

@media (max-width: 766px) {
    .course-container {
        flex-direction: column;
        align-items: center; 
        max-width: 100%; 
        padding: 0 20px; 
    }
   .static{
    margin: 10px;
    width: 100%;
   }
    .text-section {
        width: 100%; /* Full width for smaller screens */
        padding-right: 5px; /* Remove right padding as it's now full width */
        padding-left: 7px; /* Remove right padding as it's now full width */
        margin-bottom: 30px; /* Adjust bottom margin for spacing */
    }

    .cont {
        margin-left: 0; /* Remove left margin for alignment */
        width: auto; /* Allow content container to adjust width automatically */
        padding: 0 0px; /* Add padding for text content */
    }

    h1, .p1, h3 {
        width: auto; /* Allow text elements to adjust width automatically */
        margin-bottom: 10; /* Align text to the start */
        padding-bottom: 8; /* Remove left padding for alignment */
    }

    .image-gallery {
        margin-left: 0; /* Remove left margin for alignment */
        width: 100%; /* Allow image gallery to use full width */
    }

    .image-gallery .image-large {
        width: 100%; /* Full width for the large image to be responsive */
        height: auto; /* Adjust height automatically to maintain aspect ratio */
    }
}

