}
.btn2{
    background-color: #e2e7f4;
   color: rgb(22, 7, 39);
  padding: 2px;
  margin: 5px;
  width: 100px;
  height: 40px;
  border: 1px solid rgb(147, 183, 240);
  border-radius: 5px;
}
.row{
    margin-left: 0%;
    margin-right: 0%;
    display: flex;
    flex-direction: row;
}
.col-md-9{
   width: 66vw;
}
.col-md-3{
    width: 25vw;
}
@media (max-width: 767px) {

    .col-md-9{
        width: 66vw;
     }
    .row{
        margin-left: 0%;
        margin-right: 0%;
        display: flex;
        flex-direction: row;
    }
     
    }
    @media (max-width: 660px) {
        .col-md-9 {
          margin-left: 20px;
          padding: 1rem;
          width: 70vw;
      }
      .row {
        margin-left: 0%;
        margin-right: 0%;
        display: flex;
        flex-direction: row;
      }
      .col-md-3{
        width: 0px;
    }
      }
      