.container1 {
    max-width: 80vw;
    margin-top: 0px;
    padding: 0px;
    text-align: center;
    width: 980px;
    border: 1px;
    margin-bottom: 70px;
  }
  .btn_submit{
    background-color: #eceef7;
    color: rgb(22, 7, 39);
   padding: 2px;
   margin: 5px;
   width: 70px;
   height: 40px;
   border: 1px solid rgb(142, 181, 243);
   border-radius: 5px;
  }
  .hh1 {
    color: rgb(51, 8, 12);
    font-family: "Inter", sans-serif;
   font-size: 30px;
     font-weight: 500;
    line-height: 40px;
    letter-spacing: 0em;
    text-align: left;
    margin-bottom: 40px;
    margin-top: 50px; 
  }
  
  .cards {
    display: flex;
    margin-top: 30px;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background: #ffffff;
  }
  
  .dep-card1 {
    border: 0.6px solid rgb(213, 212, 212);
    width: 22%;
    height: 140px;
    box-shadow: 0 4px 8px rgba(111, 110, 110, 0.3); 
    padding: 38px, 49px, 32px, 49px;
    transition: transform 0.9s; 
  }

  .head1{
    font-size: 30px;

  }
 
 .btn1{
 
   background-color: #c9d1ee;
   color: rgb(22, 7, 39);
  padding: 2px;
  margin: 5px;
  width: 70px;
  height: 40px;
  border: 1px solid rgb(166, 196, 244);
  border-radius: 5px;
 }
.btn1:hover{
  background-color: #8e9ac9;
}
 .department_name a {
    color: rgb(15, 2, 73);
     text-decoration: none;
     font-size: 30px;
      -webkit-filter: grayscale(100%); 
    filter: grayscale(100%);
    margin-top: 30px;
    width: 172px;
    height: 172px;
    border-radius: 50%; 
  } 
  .department_name a:hover{
    color: rgb(117, 116, 113);
  }
  .profile-info1 {
    text-align: center;
  }
  
  .profile-info1 h3 {
    margin-top: 1px;
  }


   /* Departments.css */
.container-fluid {
  padding-left: 0;
  padding-right: 0;
}

.row1 {
  margin-left: 0;
  margin-right: 0;
  display: flex;
}

/* Responsive adjustments */
@media (max-width: 991px) {
  /* .sidebar {
      width: 100%;
      height: auto;
      position: relative;
  } */
  
  .col-md-9 {
      margin-left: 0;
      padding: 1rem;
  }
  .row {
    margin-left: 0;
    margin-right: 0;
    display: flex;
  }
  
  .cards {
    width: 90%;
  }
  .container1{
    width: 80vw;
   
  }
}

@media (max-width: 767px) {
.dep-card1{
  width: 200px;
}
.cards{
  width: 60vh;
}
.container1{
  width: 66vw;
 
}
.row {
  margin-left: 0%;
  margin-right: 0%;
  display: flex;
  flex-direction: row;
}

}
@media (max-width: 660px) {
  .col-md-9 {
    margin-left: 40px;
    padding: 1rem;
}
.dep-card1{
  width: 200px;
  margin-bottom: 13px;
}
.cards{
  width: 60vh;
  display: flex;
  flex-direction: column;
}
.container1{
  margin-left: 10px;
  width: 80vw;
 
}
.row {
  margin-left: 0%;
  margin-right: 0%;
  display: flex;
  flex-direction: row;
}

}
