
@tailwind base;
@tailwind components;
@tailwind utilities;




 @media (max-width: 1024px) {
  .navfield{
        font-size: 1.5vw;
        
  }
 .nav1{
   width: 20vw;
   padding: 2px;
   margin: 0px;
 }
 .nav2{
   
   width: 18vw;
   padding: 2px;
 }
 .bt{
    margin-right: 0px;
    width: 18vw;
 }
 .grid{
    margin-right: 2px;
 }
}

.hamburger-container {
    position: fixed;
    top: 70px; /* Adjust as needed */
    left: 10; /* Adjust based on your layout */
    z-index: 2; /* Above other content */
    padding: 1rem;
    padding-bottom: 10px;
  }
  
  .hamburger-menu {
    display: none; /* Hidden by default, shown in mobile view */
    cursor: pointer;
    /* Additional styling for the hamburger icon */
  }

@media (max-width: 660px) {
  .navfield{
        font-size: 12px;
  }
  .nav1{
    width: 0px;
    padding: 1px;
    margin: 0px;
    overflow-x: hidden;
    transition: 0.5s; /* Smooth transition for opening/closing */
    z-index: 1;
  }
  .nav2{
    margin-top: 8px;
    width: 15vw;
    padding: 2px;
  }
  .bt{
    margin-top: 12px;
     margin-right: 0px;
     width: 13vw;
  }
  .hamburger-menu {
    display: block;
    cursor: pointer;
  }
} 


